<template>
  <section class="foot"> 
    <div class="f_top">
      <div class="logins">
        <router-link v-if="!$store.state.memberId" to="/login">登录</router-link>
        <router-link v-if="!$store.state.memberId" to="/login?np=2">注册</router-link>
        <!-- 已经登录的时候显示  -->
        <a 
          v-if="$store.state.memberId" 
          href="javascript:;">欢迎您：{{ $store.state.memberInfo.memberName }}</a>
        <a 
          v-if="$store.state.memberId" 
          href="javascript:;"
          @click="singOut">退出</a>
      </div>
      <div class="abouts">
        <router-link to="/">首页</router-link>
        <router-link to="/myinfo/setting?id=1">关于阿康</router-link>
      </div>
    </div>
    <div class="f_bottom">
      <p>订购热线  400-9309-990</p>
      <p>售后专线 020-3705-1000</p>
      <p>copyright？2011-2017 阿康 版权所有</p>
      <p><a href="http://www.miitbeian.gov.cn/ ">粤ICP备16078801号-1</a></p>
    </div>
  </section>
</template>

<script>
export default {
  name: 'comFoot',
  data (){
    return {

    }
  },
  methods: {
    //退出
    singOut (){

      // sessionStorage.seslotoken = ''; // token
      // sessionStorage.seslouserph = ''; // 用户
      sessionStorage.seslomemberInfo = ''; // 会员ID
      this.$store.commit('minus', {
        addToken: '',
        addPhone: '',
        addMemberID: ''
      });
      // this.$store.commit('addBuyProductInfo', '');//清除信息
      // this.$router.replace({path: '/login'});
      location.reload();
    },
  },
}
</script>

<style scoped>
  @import './Css/comfoot.css';
</style>


